import Vue from 'vue';

let versionList;
export function getVueVersion(pos = 0) {
  if ( !versionList ) {
    versionList = (Vue.version || '').split('.').map(d => parseInt(d, 10));
  }
  return versionList[pos];
}

export function reactiveValueWrap(keys, self) {
  "use strict";
  const ret = {};

  keys.forEach(function (k) {
    Object.defineProperty(ret, k, {
      enumerable: true,
      configurable: false,
      get: function () {

        return self[k]
      },
      set: function (v) {

        self[k] = v
      }
    });
  });

  return ret;
}

export function warn(...args) {
  if (!console) return;

  const warnFunc = console.warn || console.log;
  warnFunc.apply(console, args);

}

const hasOwnProperty = Object.prototype.hasOwnProperty;

export function hasOwn(obj, key) {
  return hasOwnProperty.call(obj, key);
}

export function makeMap(arr, defaultValue) {
  const ret = Object.create(null);
  arr.forEach((k) => {
    ret[k] = defaultValue;
  });
  return ret;
}

export function isSupportPromise(P) {
  var isSupport = false;
  try {
    var promise = null;
    var then = null;
    try {
      promise = P.resolve();
      then = promise.then;
    } catch (e) {
      // silently ignored
    }
    if (promise instanceof P && (typeof then === 'function') && !P.cast) {
      isSupport = true;
    }
  } catch(e) {
      // silently ignored
  }

  return isSupport;
}

function toObject(val) {
	if (val === null || val === undefined) {
		throw new TypeError('Object.assign cannot be called with null or undefined');
	}

	return Object(val);
}

function objectAssignPolyfill(target, source) {
	var from;
	var to = toObject(target);
	var symbols;

	for (var s = 1; s < arguments.length; s++) {
		from = Object(arguments[s]);

		for (var key in from) {
			if (hasOwnProperty.call(from, key)) {
				to[key] = from[key];
			}
		}
	}

	return to;
};

export const objectAssign = Object['ass'+'ign'] || objectAssignPolyfill;
